import React, { useEffect } from "react";

import Navbar from "../../../components/genral_components/Navbar/Navbar";

import SubCategoriesBanner from "../../../templates/subcategories/subcategoriesbanner/SubCategoriesBanner";

import BookCoverDesignDescription from "../../../components/book_cover_design_components/book_cover_design_description/BookCoverDesignDescription";

import SubCategoriesCardLeft from "../../../templates/subcategories/sub-categories-card-left/SubCategoriesCardLeft";

import SubCategoriesCardRight from "../../../templates/subcategories/sub-categories-card-right/SubCategoriesCardRight";

import Pricing from "../../../components/services_page_components/pricing/Pricing";

import Skills from "../../../components/home_page_components/ourskills/Skills";

import Ourwork from "../../../components/home_page_components/ourwork/Ourwork";

import Footer from "../../../components/genral_components/Footer/Footer";

import pic1 from "./pic1.png"
import pic2 from "./pic2.png"
import pic3 from "./pic3.png"

import { useLocation } from "react-router-dom";

import BookCoverDesignBanner from "./BookCoverDesignBanner.jpg";

export default function VideoMarketing() {

  const location = useLocation();

  useEffect(() => {
    window.scrollTo(0, 0);
  }, [location]);
  
  return (
    <div className="video-marketing-main-container">
      <Navbar />

      <SubCategoriesBanner
        bannerHeading="Book Cover Design In The USA"
        bannerParagraph="Elevate your book's visual appeal with our professional book cover design agency."
        bannerButton1="LEARN MORE"
        bannerButton2="OUR WORK"
        bannerBackgroundImage={BookCoverDesignBanner}
      />

      <BookCoverDesignDescription
        paragraph="Experience the full range of our book cover design agency's
        capabilities, including custom illustration, 3D rendering, and photo
        manipulation services. Our team of skilled artists and designers brings
        your book's vision to life with bespoke illustrations, stunning 3D
        visuals, and expertly crafted photo manipulations. From captivating
        characters to immersive settings, we ensure that every element of your
        cover design is tailored to engage readers and convey the essence of
        your story. Partner with our agency to create a book cover that stands
        out captures attention and leaves a lasting impression. Contact us today
        to explore the creative possibilities for your book."
      />

      <SubCategoriesCardLeft
        heading="Custom 3D Renders"
        paragrapgh=" Three-dimensional images that are created especially to design a book cover are referred to as custom 3D renders. To create realistic and dynamic representations of persons, objects, or environments relevant to the book's content, skilled 3D artists use cutting-edge tools and techniques."
        picture={pic1}
      />

      <SubCategoriesCardRight
        heading="Custom illustrations"
        paragraph="Our Talented illustrators employ their imaginations and artistic skills to bring the concepts, settings, and characters of the book to life. These illustrations are skillfully created to complement the tone, mood, and genre of the book, offering an engaging visual depiction that connects with readers and aids in evoking the spirit of the narrative."
        picture={pic2}
      />

      <SubCategoriesCardLeft
        heading="Photo Manipulation"
        paragrapgh="To produce a unified and aesthetically appealing book cover design, expertly edit and combine photographic images. Expert designers can improve existing photos or produce whole new compositions using a variety of techniques, including blending, retouching, and color correction. They can efficiently portray the desired atmosphere, tone, or concept of the book by editing images, and making a strong visual impact that draws in potential readers."
        picture={pic3}
      />

      <br />
      <br />
      <br />
      <br />

      <Pricing />

      <br />
      <br />
      <br />

      <Skills />

      <br />
      <br />
      <br />

      <Ourwork />

      <Footer />
    </div>
  );
}
